@import "./window";
@import "./timeline";
@import "./statistics";
@import "./home";
@import "./taskbar";
@import "./social";
@import "./browser";
@import "./tooltip";
@import "./responsive";
@import "./print";

* {
  margin: 0;
  padding: 0;
  font-family: arial;
  transition: opacity 0.5s;
}

ul {
  list-style: none;
}

a {
  color: #7289da;

  &:visited {
    color: #a3b7ff;
  }
}

body {
  overflow: hidden;
    height: 100vh;
    width: 100vw;
}

#root {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(10, 0, 94, 1) 100%
  );
  background: url(https://raw.githubusercontent.com/Pierre-LHOSTE/Pierre-LHOSTE.github.io/assets/wallpaper.png);
  background-size: cover;
  background-position: center;
}
